.form {
  border: 1px solid black;
  border-radius: 5px;
  box-shadow: 5px 5px 5px #000000;
  background-color: #fff;
  min-width: 10vmin;
  min-height: 8vmin;
  text-align: center;
  display: block;
  color: #000;
  padding: 2rem;
}

.label {
  font-weight: bold;
  text-align: center;
  display: block;
  color: #000;
}

.input {
  text-align: center;
  display: inline-block;
  color: #000;
  font-size: large;
  border-radius: 10px;
  padding: 0.5rem;
}

.error {
  color: red;
}
